



























import Vue from 'vue';
import Component from 'vue-class-component';

@Component({
  name: 'DemoDemo1'
})
export default class DemoDemo1 extends Vue {

  public state = {
    open: true
  };

  get drawerStyle() {
    return {minHeight: document.documentElement.clientHeight + 'px'};
  }

  public onOpenChange(...args) {
    console.log(args);
    this.state.open = !this.state.open;
  }
}
