



























import Vue from 'vue';
import Component from 'vue-class-component';

@Component({
  name: 'DemoDemo2'
})
export default class DemoDemo2 extends Vue {


  public state = {
    docked: true,
    open: true
  };

  get drawerStyle() {
    return {minHeight: document.documentElement.clientHeight + 'px'};
  }

  public onDock(d) {
    this.state[d] = !this.state[d];
  }
}
